// ロゴのhover処理
#logo{
  line-height: 1 !important;
  a{
    &:hover{
      text-decoration: none;
    }
  }
}
@media (max-width: 575.98px) {
  #logo{
    font-size: 2rem;
  }
}

//ベースカラー
$base-color:#FFAD36;

// 背景
.bg-custom{
  background-color: $base-color;
}

a.text-light:focus, a.text-light:hover {
  color: #fff !important;
}